	module.exports = {"data":{"content":{"content":"<h2 id=前言>前言</h2>\n<p>一直收聽知乎無限 live 的時候，我都會想：如何才能更有效率地收聽這些計畫內的 live 呢？於是我心血來潮，把無限 live 的資料拿了下來，然後做一個數據流的網站。這個網站展示和源碼已經放在 <a href=\"https://github.com/calpa/zhihu-unlimited-web\">Github</a> 。這個項目可能適合那些喜歡看到很多數據的同學...</p>\n<p><a data-fancybox=\"gallery\" href=\"https://i.imgur.com/TaTf7dqh.jpg\"><img class=\"lozad d-block mx-auto\" data-src=https://i.imgur.com/TaTf7dql.jpg title=\"Overview\" /></a></p>\n<h2 id=主要功能>主要功能</h2>\n<ol>\n<li>展示所有無限 live 計畫下面 live 資料，包括：日期，費用，參加人數，分數，以及講者。</li>\n<li>提供過濾名稱功能</li>\n<li>提供每頁展示特定資料數量的選項（10,20,100）。</li>\n</ol>\n<h2 id=數據庫>數據庫</h2>\n<p>透過簡單的 nodejs 腳本來獲取知乎 API，並儲存到 <a href=\"https://github.com/calpa/zhihu-unlimited-live\">zhihu-unlimited-live</a></p>\n","title":"知乎無限 live 數據流網站","createdDate":"2018-03-28T20:49+08:00","headerImgur":null,"id":"1fQm8RP6MCIq842gG8C66C","toc":["前言","主要功能","數據庫"],"tags":"Zhihu, JavaScript"},"latestPosts":{"totalCount":60,"edges":[{"node":{"title":"【網易考拉校招】部門總監面對面-直播答疑邀約（附網易內推碼B222E54）","url":"netease-kaola-live","createdDate":"2018-08-01T20:00+08:00"}},{"node":{"title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","url":"regenerator-runtime-is-not-defined","createdDate":"2018-07-29T20:00+08:00"}},{"node":{"title":"關於作者","url":"about-calpa-liu","createdDate":"2018-05-01T20:00+08:00"}},{"node":{"title":"【方法】在直接複製貼上內容的時候，可能已經不知不覺注入了一段不可視的「空白」","url":"insert-zero-width-characters","createdDate":"2018-04-06T23:00+08:00"}},{"node":{"title":"知乎無限 live 數據流網站","url":"zhihu-unlimited-lives-website","createdDate":"2018-03-28T20:49+08:00"}},{"node":{"title":"Gatsbyjs async/await 更新報錯解決方法","url":"gatsybjs-async-await-upgrade-bug-solution","createdDate":"2018-03-21T20:00+08:00"}}]}},"pathContext":{"id":"1fQm8RP6MCIq842gG8C66C"}}

